import { useState, useEffect, useCallback, useRef } from 'react';
import { isMobileDevice, isAndroidDevice, getAndroidNavigationBarHeight, getKeyboardHeight, debounce, throttle } from '../utils/layoutUtils';
/**
 * 虚拟键盘处理 Hook
 * 提供虚拟键盘状态检测和处理功能
 */
export const useVirtualKeyboard = () => {
    const [keyboardState, setKeyboardState] = useState({
        isVisible: false,
        height: 0,
    });
    const isMobile = useRef(isMobileDevice());
    const isAndroid = useRef(isAndroidDevice());
    const navigationBarHeight = useRef(getAndroidNavigationBarHeight());
    const observerRef = useRef(null);
    // 更新键盘状态
    const updateKeyboardState = useCallback((config) => {
        const isVisible = config.keyboardHeight > 0;
        setKeyboardState(prev => {
            // 只有状态真正改变时才更新
            if (prev.isVisible !== isVisible || prev.height !== config.keyboardHeight) {
                return {
                    isVisible,
                    height: config.keyboardHeight,
                };
            }
            return prev;
        });
        // 更新 CSS 变量
        document.documentElement.style.setProperty('--keyboard-height', `${config.keyboardHeight}px`);
        // 更新安卓导航栏高度变量
        if (isAndroid.current) {
            const navHeight = getAndroidNavigationBarHeight();
            navigationBarHeight.current = navHeight;
            document.documentElement.style.setProperty('--android-navigation-bar-height', `${navHeight}px`);
        }
    }, []);
    // 处理视口大小变化 - 优化性能，减少计算频率
    const handleViewportResize = useCallback(throttle(() => {
        if (!isMobile.current)
            return;
        // 获取安全区域值 - 缓存计算结果
        const safeAreaTop = parseInt(getComputedStyle(document.documentElement).getPropertyValue('--safe-area-inset-top')) || 0;
        const safeAreaBottom = parseInt(getComputedStyle(document.documentElement).getPropertyValue('--safe-area-inset-bottom')) || 0;
        let keyboardHeight = 0;
        if (isAndroid.current) {
            // 安卓设备特殊处理
            const viewportHeight = window.visualViewport?.height || window.innerHeight;
            const screenHeight = window.screen.height;
            const windowHeight = window.innerHeight;
            const navBarHeight = getAndroidNavigationBarHeight();
            // 计算键盘高度，减去导航栏高度
            if (viewportHeight < windowHeight * 0.8) {
                keyboardHeight = windowHeight - viewportHeight - navBarHeight;
            }
            // 确保键盘高度合理，且不为负数
            keyboardHeight = Math.max(0, Math.min(keyboardHeight, screenHeight * 0.5));
        }
        else {
            // iOS和其他设备使用原有逻辑
            keyboardHeight = getKeyboardHeight({
                isMobile: true,
                headerHeight: 60,
                keyboardHeight: 0,
                safeAreaTop,
                safeAreaBottom,
            });
        }
        updateKeyboardState({ keyboardHeight });
    }, 200), [updateKeyboardState]); // 增加节流时间到200ms，减少计算频率
    // 处理输入框焦点事件
    const handleInputFocus = useCallback(() => {
        if (!isMobile.current)
            return;
        // 延迟检测，确保键盘完全弹出
        setTimeout(handleViewportResize, 300);
    }, [handleViewportResize]);
    const handleInputBlur = useCallback(() => {
        if (!isMobile.current)
            return;
        // 延迟检测，确保键盘完全收起
        setTimeout(() => {
            updateKeyboardState({ keyboardHeight: 0 });
        }, 100);
    }, [updateKeyboardState]);
    // 设置输入框事件监听
    const setupInputListeners = useCallback((inputElement) => {
        if (!inputElement || !isMobile.current)
            return;
        inputElement.addEventListener('focus', handleInputFocus);
        inputElement.addEventListener('blur', handleInputBlur);
        return () => {
            inputElement.removeEventListener('focus', handleInputFocus);
            inputElement.removeEventListener('blur', handleInputBlur);
        };
    }, [handleInputFocus, handleInputBlur]);
    // 初始化监听器 - 优化性能，减少事件监听器数量
    useEffect(() => {
        if (!isMobile.current)
            return;
        // 监听视口大小变化 - 优先使用visualViewport
        if (window.visualViewport) {
            window.visualViewport.addEventListener('resize', handleViewportResize);
        }
        else {
            // 如果不支持visualViewport，则使用window.resize
            window.addEventListener('resize', handleViewportResize);
        }
        // 创建 ResizeObserver 监听根元素变化 - 增加防抖延迟
        observerRef.current = new ResizeObserver(debounce(handleViewportResize, 200) // 增加到200ms
        );
        observerRef.current.observe(document.documentElement);
        // 初始检测
        handleViewportResize();
        return () => {
            // 清理监听器
            if (window.visualViewport) {
                window.visualViewport.removeEventListener('resize', handleViewportResize);
            }
            else {
                window.removeEventListener('resize', handleViewportResize);
            }
            if (observerRef.current) {
                observerRef.current.disconnect();
            }
        };
    }, [handleViewportResize]);
    // 添加键盘状态变化的 body 类
    useEffect(() => {
        if (keyboardState.isVisible) {
            document.body.classList.add('keyboard-visible');
        }
        else {
            document.body.classList.remove('keyboard-visible');
        }
    }, [keyboardState.isVisible]);
    // 添加安卓设备检测类
    useEffect(() => {
        if (isAndroid.current) {
            document.body.classList.add('android-device');
            // 初始化安卓导航栏高度变量
            const navHeight = getAndroidNavigationBarHeight();
            navigationBarHeight.current = navHeight;
            document.documentElement.style.setProperty('--android-navigation-bar-height', `${navHeight}px`);
        }
        else {
            document.body.classList.remove('android-device');
        }
    }, []);
    return {
        keyboardState,
        setupInputListeners,
        hideKeyboard: () => updateKeyboardState({ keyboardHeight: 0 }),
    };
};
