import { useState, useEffect, useRef, useCallback } from 'react';
/**
 * 消息滚动处理 Hook
 * 提供自动滚动和用户滚动检测功能
 */
export const useMessageScroll = (messages, isVisible, isStreaming = false) => {
    const [userScrolledUp, setUserScrolledUp] = useState(false);
    const [isUserActivelyScrolling, setIsUserActivelyScrolling] = useState(false);
    const messagesContainerRef = useRef(null);
    const lastMessageRef = useRef(null);
    const observerRef = useRef(null);
    const scrollTimeoutRef = useRef(null);
    const userScrollTimeoutRef = useRef(null);
    // 检查是否滚动到底部
    const isAtBottom = useCallback(() => {
        const container = messagesContainerRef.current;
        if (!container)
            return true;
        const { scrollTop, scrollHeight, clientHeight } = container;
        const threshold = 100; // 100px 的阈值
        return scrollHeight - scrollTop <= clientHeight + threshold;
    }, []);
    // 滚动到底部
    const scrollToBottom = useCallback((force = false) => {
        if (!isVisible)
            return;
        // 如果用户正在主动滚动或者向上滚动了且不是强制滚动，则不自动滚动
        if ((isUserActivelyScrolling || userScrolledUp) && !force)
            return;
        const container = messagesContainerRef.current;
        if (!container)
            return;
        // 使用 requestAnimationFrame 优化滚动性能
        requestAnimationFrame(() => {
            // 强制滚动到绝对底部，确保完全可见
            container.scrollTo({
                top: container.scrollHeight,
                behavior: force ? 'auto' : 'smooth',
            });
            // 如果是强制滚动，再次确保滚动到底部（处理移动端可能的延迟）
            if (force) {
                setTimeout(() => {
                    container.scrollTo({
                        top: container.scrollHeight,
                        behavior: 'auto',
                    });
                }, 50);
            }
        });
    }, [isVisible, userScrolledUp, isUserActivelyScrolling]);
    // 处理滚动事件 - 优化性能，减少状态更新频率
    const handleScroll = useCallback(() => {
        // 清除之前的超时
        if (scrollTimeoutRef.current) {
            clearTimeout(scrollTimeoutRef.current);
        }
        // 清除用户滚动状态的超时
        if (userScrollTimeoutRef.current) {
            clearTimeout(userScrollTimeoutRef.current);
        }
        // 标记用户正在主动滚动
        setIsUserActivelyScrolling(true);
        // 防抖处理滚动事件 - 增加延迟时间，减少频繁更新
        scrollTimeoutRef.current = setTimeout(() => {
            const atBottom = isAtBottom();
            setUserScrolledUp(!atBottom);
            // 如果用户停止滚动一段时间后，重置主动滚动状态 - 增加延迟时间
            userScrollTimeoutRef.current = setTimeout(() => {
                setIsUserActivelyScrolling(false);
            }, 1500); // 增加到1.5秒
        }, 150); // 增加到150ms
    }, [isAtBottom]);
    // 设置 Intersection Observer 监听最后一条消息
    useEffect(() => {
        if (!isVisible)
            return;
        observerRef.current = new IntersectionObserver(([entry]) => {
            // 如果最后一条消息可见，说明用户在底部
            if (entry.isIntersecting) {
                setUserScrolledUp(false);
                setIsUserActivelyScrolling(false);
            }
        }, {
            root: messagesContainerRef.current,
            threshold: 0.1,
        });
        if (lastMessageRef.current) {
            observerRef.current.observe(lastMessageRef.current);
        }
        return () => {
            if (observerRef.current) {
                observerRef.current.disconnect();
            }
        };
    }, [isVisible, messages.length]);
    // 监听消息容器滚动 - 优化性能
    useEffect(() => {
        const container = messagesContainerRef.current;
        if (!container || !isVisible)
            return;
        // 使用被动事件监听器提高滚动性能
        container.addEventListener('scroll', handleScroll, { passive: true });
        return () => {
            container.removeEventListener('scroll', handleScroll);
            if (scrollTimeoutRef.current) {
                clearTimeout(scrollTimeoutRef.current);
            }
            if (userScrollTimeoutRef.current) {
                clearTimeout(userScrollTimeoutRef.current);
            }
        };
    }, [isVisible, handleScroll]);
    // 当消息变化时，自动滚动到底部
    useEffect(() => {
        if (messages.length === 0)
            return;
        // 如果是用户发送的消息，强制滚动到底部
        const lastMessage = messages[messages.length - 1];
        if (lastMessage.role === 'user') {
            setUserScrolledUp(false);
            setIsUserActivelyScrolling(false);
            // 延迟滚动确保 DOM 更新完成
            setTimeout(() => scrollToBottom(true), 100);
        }
        else if (!userScrolledUp && !isUserActivelyScrolling) {
            // 如果是 AI 回复且用户没有向上滚动且没有主动滚动，自动滚动
            scrollToBottom();
        }
    }, [messages, userScrolledUp, isUserActivelyScrolling, scrollToBottom]);
    // 当组件显示/隐藏时处理滚动
    useEffect(() => {
        if (isVisible && !userScrolledUp && !isUserActivelyScrolling) {
            scrollToBottom();
        }
    }, [isVisible, userScrolledUp, isUserActivelyScrolling, scrollToBottom]);
    // 当流式传输状态变化时，重置用户滚动状态
    useEffect(() => {
        if (!isStreaming) {
            setIsUserActivelyScrolling(false);
        }
    }, [isStreaming]);
    // 重置滚动状态
    const resetScrollState = useCallback(() => {
        setUserScrolledUp(false);
        setIsUserActivelyScrolling(false);
        scrollToBottom(true);
    }, [scrollToBottom]);
    return {
        messagesContainerRef,
        lastMessageRef,
        userScrolledUp,
        setUserScrolledUp,
        scrollToBottom,
        resetScrollState,
    };
};
