/**
 * 格式化星耀数据为可读字符串
 * @param stars 星耀数组
 * @returns 格式化后的星耀字符串
 */
const formatStars = (stars) => {
    if (!stars || stars.length === 0)
        return '无';
    return stars.join('、');
};
/**
 * 格式化宫位数据为可读字符串
 * @param palace 宫位数据
 * @returns 格式化后的宫位信息字符串
 */
const formatPalace = (palace) => {
    const majorStars = palace.majorStars?.map(star => star.name) || [];
    const minorStars = palace.minorStars?.map(star => star.name) || [];
    const adjectiveStars = palace.adjectiveStars?.map(star => star.name) || [];
    return `
宫位：${palace.name}
天干地支：${palace.heavenlyStem}${palace.earthlyBranch}
主星：${formatStars(majorStars)}
辅星：${formatStars(minorStars)}
杂耀：${formatStars(adjectiveStars)}
长生12神：${palace.changsheng12 || '无'}
博士12神：${palace.boshi12 || '无'}
将前12神：${palace.jiangqian12 || '无'}
岁前12神：${palace.suiqian12 || '无'}
大限：${palace.decadal?.range?.[0]}-${palace.decadal?.range?.[1]}岁
小限：${palace.ages?.join('、') || '无'}
${palace.isBodyPalace ? '【身宫】' : ''}
${palace.isOriginalPalace ? '【来因宫】' : ''}
  `.trim();
};
/**
 * 将星盘数据格式化为AI可理解的文本格式
 * @param astrolabe 星盘数据
 * @param currentTime 当前时间
 * @returns 格式化后的星盘信息文本
 */
export const formatAstrolabeForAI = (astrolabe, currentTime = new Date(), gender) => {
    if (!astrolabe) {
        return '暂无星盘数据';
    }
    const formattedTime = currentTime.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        hour12: false
    });
    let result = `
=== 紫微斗数星盘数据 ===
当前时间：${formattedTime}

基本信息：
性别：${gender}
阳历日期：${astrolabe.solarDate}
农历日期：${astrolabe.lunarDate}
干支纪年：${astrolabe.chineseDate}
出生时辰：${astrolabe.time}（${astrolabe.timeRange}）
星座：${astrolabe.sign}
生肖：${astrolabe.zodiac}
命宫地支：${astrolabe.earthlyBranchOfSoulPalace}
身宫地支：${astrolabe.earthlyBranchOfBodyPalace}
命主：${astrolabe.soul}
身主：${astrolabe.body}
五行局：${astrolabe.fiveElementsClass}

十二宫位详情：
`;
    // 添加十二宫位信息
    if (astrolabe.palaces && astrolabe.palaces.length > 0) {
        astrolabe.palaces.forEach((palace, index) => {
            result += `\n--- 第${index + 1}宫 ---\n`;
            result += formatPalace(palace);
            result += '\n\n';
        });
    }
    // 添加当前运限信息
    if (astrolabe.horoscope) {
        const horoscope = astrolabe.horoscope();
        if (horoscope) {
            result += `
=== 当前运限信息 ===
查询日期：${horoscope.solarDate}
农历日期：${horoscope.lunarDate}

大限：
天干地支：${horoscope.decadal.heavenlyStem}${horoscope.decadal.earthlyBranch}
四化星：${formatStars(horoscope.decadal.mutagen || [])}

小限：
虚岁：${horoscope.age.nominalAge}
天干地支：${horoscope.age.heavenlyStem}${horoscope.age.earthlyBranch}

流年：
天干地支：${horoscope.yearly.heavenlyStem}${horoscope.yearly.earthlyBranch}
四化星：${formatStars(horoscope.yearly.mutagen || [])}

流月：
天干地支：${horoscope.monthly.heavenlyStem}${horoscope.monthly.earthlyBranch}

流日：
天干地支：${horoscope.daily.heavenlyStem}${horoscope.daily.earthlyBranch}

流时：
天干地支：${horoscope.hourly.heavenlyStem}${horoscope.hourly.earthlyBranch}
`;
        }
    }
    return result.trim();
};
