import React from 'react';
const QuestionPanel = ({ questionCategories, activeQuestionTab, onTabChange, onQuestionClick, showQuestionPanel, }) => {
    if (!showQuestionPanel)
        return null;
    const activeCategory = questionCategories.find(c => c.name === activeQuestionTab);
    return (React.createElement("div", { className: "question-panel" },
        React.createElement("div", { className: "question-tabs" }, questionCategories.map((category) => (React.createElement("button", { key: category.name, className: `tab-button question-tab-button ${activeQuestionTab === category.name ? "active" : ""}`, onClick: () => onTabChange(category.name) }, category.name)))),
        React.createElement("div", { className: "question-content" }, activeCategory?.subCategories.map((subCategory) => (React.createElement("div", { key: subCategory.name, className: "question-group" },
            React.createElement("h4", { className: "category-heading" }, subCategory.name),
            React.createElement("div", { className: "questions-wrapper" }, subCategory.questions.map((question, index) => (React.createElement("p", { key: index, onClick: () => onQuestionClick(question), className: "question-text" }, question))))))))));
};
export default QuestionPanel;
