import { useState, useEffect, useCallback } from 'react';
import { getLayoutConfig, isMobileDevice, updateLayoutCSSVariables, createLayoutObserver } from '../utils/layoutUtils';
/**
 * 聊天布局 Hook
 * 提供响应式布局管理和 CSS Grid 布局配置
 */
export const useChatLayout = () => {
    const [layoutConfig, setLayoutConfig] = useState(getLayoutConfig());
    const [isInitialized, setIsInitialized] = useState(false);
    // 更新布局配置
    const updateLayout = useCallback((newConfig) => {
        const updatedConfig = { ...layoutConfig, ...newConfig };
        setLayoutConfig(updatedConfig);
        updateLayoutCSSVariables(updatedConfig);
    }, [layoutConfig]);
    // 初始化布局
    const initializeLayout = useCallback(() => {
        const config = getLayoutConfig();
        updateLayoutCSSVariables(config);
        setLayoutConfig(config);
        setIsInitialized(true);
    }, []);
    // 处理窗口大小变化
    const handleResize = useCallback(() => {
        const config = getLayoutConfig();
        updateLayout(config);
    }, [updateLayout]);
    // 处理移动端特殊布局
    const handleMobileLayout = useCallback(() => {
        if (!layoutConfig.isMobile)
            return;
        // 为移动端添加特殊的 body 类
        document.body.classList.add('mobile-chat');
        // 设置移动端特定的 CSS 变量
        document.documentElement.style.setProperty('--chat-font-size', '16px');
    }, [layoutConfig.isMobile]);
    // 处理桌面端布局
    const handleDesktopLayout = useCallback(() => {
        document.body.classList.remove('mobile-chat');
        document.documentElement.style.setProperty('--chat-font-size', '14px');
    }, []);
    // 初始化监听器
    useEffect(() => {
        if (isInitialized)
            return;
        // 初始化布局
        initializeLayout();
        // 创建布局观察器
        const observer = createLayoutObserver(handleResize);
        observer.observe(document.documentElement);
        // 监听窗口大小变化
        window.addEventListener('resize', handleResize);
        // 根据设备类型处理布局
        if (layoutConfig.isMobile) {
            handleMobileLayout();
        }
        else {
            handleDesktopLayout();
        }
        return () => {
            observer.disconnect();
            window.removeEventListener('resize', handleResize);
        };
    }, [
        isInitialized,
        initializeLayout,
        handleResize,
        layoutConfig.isMobile,
        handleMobileLayout,
        handleDesktopLayout
    ]);
    // 监听设备类型变化
    useEffect(() => {
        const handleOrientationChange = () => {
            const currentIsMobile = isMobileDevice();
            if (currentIsMobile !== layoutConfig.isMobile) {
                updateLayout({ isMobile: currentIsMobile });
            }
        };
        window.addEventListener('orientationchange', handleOrientationChange);
        return () => {
            window.removeEventListener('orientationchange', handleOrientationChange);
        };
    }, [layoutConfig.isMobile, updateLayout]);
    // 生成 CSS Grid 模板样式
    const getGridTemplateStyle = useCallback(() => {
        if (layoutConfig.isMobile) {
            // 检查键盘是否可见
            const isKeyboardVisible = document.body.classList.contains('keyboard-visible');
            if (isKeyboardVisible) {
                return {
                    gridTemplateRows: `
            var(--header-height)
            minmax(0, 1fr)
            var(--keyboard-height)
            auto
          `,
                };
            }
            return {
                gridTemplateRows: `
          var(--header-height)
          minmax(0, 1fr)
          auto
        `,
            };
        }
        return {
            gridTemplateRows: '1fr auto',
        };
    }, [layoutConfig.isMobile]);
    // 生成容器样式
    const getContainerStyle = useCallback(() => {
        const baseStyle = {
            display: 'grid',
            height: '100%',
            minHeight: 0,
            overflow: 'hidden',
        };
        if (layoutConfig.isMobile) {
            return {
                ...baseStyle,
                position: 'fixed',
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                height: '100dvh',
                width: '100%',
                ...getGridTemplateStyle(),
            };
        }
        return {
            ...baseStyle,
            ...getGridTemplateStyle(),
        };
    }, [layoutConfig.isMobile, getGridTemplateStyle]);
    // 生成消息区域样式
    const getMessagesContainerStyle = useCallback(() => {
        const baseStyle = {
            overflowY: 'auto',
            minHeight: 0,
            scrollSnapType: 'y proximity',
        };
        if (layoutConfig.isMobile) {
            return {
                ...baseStyle,
                paddingTop: 'var(--safe-area-top)',
                paddingBottom: 'var(--safe-area-bottom)',
                // 移除固定 margin 计算，使用 Grid 自动分配空间
                marginTop: 0,
                marginBottom: 0,
            };
        }
        return {
            ...baseStyle,
            padding: '15px',
        };
    }, [layoutConfig.isMobile]);
    // 生成输入区域样式
    const getInputContainerStyle = useCallback(() => {
        const baseStyle = {
            flexShrink: 0,
            backgroundColor: 'var(--background-color)',
        };
        if (layoutConfig.isMobile) {
            return {
                ...baseStyle,
                position: 'sticky',
                bottom: 0,
                zIndex: 100,
                paddingBottom: 'var(--safe-area-bottom)',
                borderTop: '1px solid var(--border-color)',
            };
        }
        return {
            ...baseStyle,
            padding: '10px',
        };
    }, [layoutConfig.isMobile]);
    return {
        layoutConfig,
        isInitialized,
        getContainerStyle,
        getMessagesContainerStyle,
        getInputContainerStyle,
        updateLayout,
    };
};
