import { useState, useEffect, useCallback } from 'react';
/**
 * 消息存储和分页Hook
 * 实现消息的本地存储和分页加载，优化内存使用
 */
export const useMessageStorage = ({ maxMessagesInMemory = 100, pageSize = 20, storageKey = 'ai-chat-messages' } = {}) => {
    const [allMessages, setAllMessages] = useState([]);
    const [visibleMessages, setVisibleMessages] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [isLoading, setIsLoading] = useState(false);
    const [hasMore, setHasMore] = useState(true);
    // 从本地存储加载消息
    const loadMessagesFromStorage = useCallback(() => {
        try {
            const storedMessages = localStorage.getItem(storageKey);
            if (storedMessages) {
                const parsedMessages = JSON.parse(storedMessages);
                // 验证消息格式
                if (Array.isArray(parsedMessages) && parsedMessages.every(msg => msg && typeof msg === 'object' &&
                    (msg.role === 'user' || msg.role === 'assistant') &&
                    typeof msg.content === 'string')) {
                    return parsedMessages;
                }
            }
        }
        catch (error) {
            console.error('从本地存储加载消息失败:', error);
        }
        return [];
    }, [storageKey]);
    // 保存消息到本地存储
    const saveMessagesToStorage = useCallback((messages) => {
        try {
            localStorage.setItem(storageKey, JSON.stringify(messages));
        }
        catch (error) {
            console.error('保存消息到本地存储失败:', error);
            // 如果存储失败，可能是存储空间不足，尝试清理旧消息
            try {
                // 保留最近的消息
                const recentMessages = messages.slice(-maxMessagesInMemory);
                localStorage.setItem(storageKey, JSON.stringify(recentMessages));
            }
            catch (cleanupError) {
                console.error('清理旧消息失败:', cleanupError);
            }
        }
    }, [storageKey, maxMessagesInMemory]);
    // 初始化时从本地存储加载消息
    useEffect(() => {
        const messages = loadMessagesFromStorage();
        setAllMessages(messages);
        // 计算初始可见消息
        const initialVisible = messages.slice(-pageSize);
        setVisibleMessages(initialVisible);
        setCurrentPage(1);
        setHasMore(messages.length > pageSize);
    }, [loadMessagesFromStorage, pageSize]);
    // 添加新消息
    const addMessages = useCallback((newMessages) => {
        setAllMessages(prev => {
            const updated = [...prev, ...newMessages];
            // 限制内存中的消息数量
            if (updated.length > maxMessagesInMemory) {
                // 保留最新的消息
                const trimmed = updated.slice(-maxMessagesInMemory);
                saveMessagesToStorage(trimmed);
                return trimmed;
            }
            saveMessagesToStorage(updated);
            return updated;
        });
        // 如果当前在最后一页，更新可见消息
        setVisibleMessages(prev => [...prev, ...newMessages]);
    }, [maxMessagesInMemory, saveMessagesToStorage]);
    // 加载更多消息（分页）
    const loadMoreMessages = useCallback(() => {
        if (isLoading || !hasMore)
            return;
        setIsLoading(true);
        // 使用setTimeout避免阻塞UI
        setTimeout(() => {
            const nextPage = currentPage + 1;
            const startIndex = Math.max(0, allMessages.length - nextPage * pageSize);
            const endIndex = allMessages.length - (nextPage - 1) * pageSize;
            if (startIndex <= 0) {
                // 已经到达第一页
                const newVisibleMessages = allMessages.slice(0, endIndex);
                setVisibleMessages(newVisibleMessages);
                setHasMore(false);
            }
            else {
                // 加载上一页的消息
                const newMessages = allMessages.slice(startIndex, endIndex);
                setVisibleMessages(prev => [...newMessages, ...prev]);
                setCurrentPage(nextPage);
            }
            setIsLoading(false);
        }, 0);
    }, [allMessages, currentPage, hasMore, isLoading, pageSize]);
    // 清空所有消息
    const clearMessages = useCallback(() => {
        setAllMessages([]);
        setVisibleMessages([]);
        setCurrentPage(1);
        setHasMore(false);
        try {
            localStorage.removeItem(storageKey);
        }
        catch (error) {
            console.error('清空本地存储失败:', error);
        }
    }, [storageKey]);
    // 重置到最新消息
    const resetToLatest = useCallback(() => {
        const latestMessages = allMessages.slice(-pageSize);
        setVisibleMessages(latestMessages);
        setCurrentPage(1);
        setHasMore(allMessages.length > pageSize);
    }, [allMessages, pageSize]);
    return {
        allMessages,
        visibleMessages,
        currentPage,
        isLoading,
        hasMore,
        addMessages,
        loadMoreMessages,
        clearMessages,
        resetToLatest
    };
};
