/**
 * 检测是否为移动设备
 */
export const isMobileDevice = () => {
    return /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
};
/**
 * 检测是否为安卓设备
 */
export const isAndroidDevice = () => {
    return /Android/i.test(navigator.userAgent);
};
/**
 * 检测安卓设备上的小白条（导航手势区域）
 * 返回小白条的估计高度（像素）
 */
export const getAndroidNavigationBarHeight = () => {
    if (!isAndroidDevice())
        return 0;
    // 方法1：通过CSS env()变量获取
    const safeAreaBottom = getSafeAreaInset('bottom');
    // 方法2：通过屏幕和窗口高度差计算
    const screenHeight = window.screen.height;
    const windowHeight = window.innerHeight;
    const viewportHeight = window.visualViewport?.height || windowHeight;
    // 如果窗口高度小于屏幕高度，可能存在导航栏
    const heightDiff = screenHeight - windowHeight;
    // 方法3：通过window.innerWidth和screen.width判断是否有导航栏
    const screenWidth = window.screen.width;
    const windowWidth = window.innerWidth;
    const widthDiff = Math.abs(screenWidth - windowWidth);
    // 综合判断：如果安全区域底部大于0，或者高度差大于0，或者宽度差大于0
    // 则认为存在导航栏，返回估计的高度
    if (safeAreaBottom > 0) {
        return safeAreaBottom;
    }
    // 安卓导航栏通常在24-48px之间
    if (heightDiff > 0 && heightDiff <= 100) {
        return heightDiff;
    }
    // 如果高度差太大，可能是键盘弹出，不计算为导航栏
    if (heightDiff > 100) {
        return 0;
    }
    // 默认安卓导航栏高度
    return widthDiff > 0 ? 48 : 0;
};
/**
 * 获取设备布局配置
 */
export const getLayoutConfig = () => {
    const isMobile = isMobileDevice();
    return {
        isMobile,
        headerHeight: isMobile ? 60 : 0,
        keyboardHeight: 0,
        safeAreaTop: getSafeAreaInset('top'),
        safeAreaBottom: getSafeAreaInset('bottom'),
    };
};
/**
 * 获取安全区域插入值
 */
export const getSafeAreaInset = (position) => {
    if (typeof CSS !== 'undefined' && CSS.supports && CSS.supports('env', 'safe-area-inset-top')) {
        const value = getComputedStyle(document.documentElement)
            .getPropertyValue(`env(safe-area-inset-${position})`);
        return parseInt(value) || 0;
    }
    return 0;
};
/**
 * 计算可用高度
 */
export const calculateAvailableHeight = (config) => {
    const viewportHeight = window.visualViewport?.height || window.innerHeight;
    return viewportHeight - config.headerHeight - config.safeAreaTop - config.safeAreaBottom;
};
/**
 * 检测虚拟键盘是否可见
 */
export const isKeyboardVisible = (config) => {
    if (!config.isMobile)
        return false;
    const viewportHeight = window.visualViewport?.height || window.innerHeight;
    const windowHeight = window.innerHeight;
    // 如果视口高度明显小于窗口高度，说明键盘可能弹出了
    return viewportHeight < windowHeight * 0.75;
};
/**
 * 获取虚拟键盘高度
 */
export const getKeyboardHeight = (config) => {
    if (!isKeyboardVisible(config))
        return 0;
    const viewportHeight = window.visualViewport?.height || window.innerHeight;
    const windowHeight = window.innerHeight;
    return windowHeight - viewportHeight;
};
/**
 * 更新 CSS 变量以响应布局变化
 */
export const updateLayoutCSSVariables = (config) => {
    const root = document.documentElement;
    root.style.setProperty('--keyboard-height', `${config.keyboardHeight}px`);
    root.style.setProperty('--safe-area-top', `${config.safeAreaTop}px`);
    root.style.setProperty('--safe-area-bottom', `${config.safeAreaBottom}px`);
    root.style.setProperty('--header-height', `${config.headerHeight}px`);
};
/**
 * 创建响应式布局观察器
 */
export const createLayoutObserver = (onLayoutChange) => {
    return new ResizeObserver(() => {
        const config = getLayoutConfig();
        config.keyboardHeight = getKeyboardHeight(config);
        updateLayoutCSSVariables(config);
        onLayoutChange(config);
    });
};
/**
 * 防抖函数
 */
export const debounce = (func, delay) => {
    let timeoutId;
    return ((...args) => {
        clearTimeout(timeoutId);
        timeoutId = setTimeout(() => func.apply(null, args), delay);
    });
};
/**
 * 节流函数
 */
export const throttle = (func, delay) => {
    let lastCall = 0;
    return ((...args) => {
        const now = Date.now();
        if (now - lastCall >= delay) {
            lastCall = now;
            func.apply(null, args);
        }
    });
};
