import { useState, useEffect, useRef, useCallback, useMemo } from 'react';
/**
 * 虚拟滚动Hook
 * 支持动态高度的虚拟滚动，优化长列表性能
 */
export const useVirtualScroll = (items, containerRef, options = {}) => {
    const { itemHeight = 80, // 默认项目高度
    overscan = 5, // 预渲染的项目数量
    defaultHeight = 80, // 默认高度
     } = options;
    const [scrollTop, setScrollTop] = useState(0);
    const [containerHeight, setContainerHeight] = useState(0);
    const [measuredItems, setMeasuredItems] = useState(new Map());
    const [isScrolling, setIsScrolling] = useState(false);
    const scrollTimeoutRef = useRef(null);
    // 计算总高度
    const totalHeight = useMemo(() => {
        let height = 0;
        for (let i = 0; i < items.length; i++) {
            const item = items[i];
            const measured = measuredItems.get(`${item.role}-${i}`);
            height += measured?.height || defaultHeight;
        }
        return height;
    }, [items, measuredItems, defaultHeight]);
    // 计算可见项目
    const visibleRange = useMemo(() => {
        let startOffset = 0;
        let startIndex = 0;
        let endIndex = items.length;
        // 找到起始索引
        for (let i = 0; i < items.length; i++) {
            const item = items[i];
            const measured = measuredItems.get(`${item.role}-${i}`);
            const itemHeight = measured?.height || defaultHeight;
            if (startOffset + itemHeight > scrollTop) {
                startIndex = Math.max(0, i - overscan);
                break;
            }
            startOffset += itemHeight;
        }
        // 找到结束索引
        let currentOffset = startOffset;
        for (let i = startIndex; i < items.length; i++) {
            const item = items[i];
            const measured = measuredItems.get(`${item.role}-${i}`);
            const itemHeight = measured?.height || defaultHeight;
            if (currentOffset > scrollTop + containerHeight) {
                endIndex = Math.min(items.length, i + overscan);
                break;
            }
            currentOffset += itemHeight;
        }
        return { startIndex, endIndex, startOffset };
    }, [items, scrollTop, containerHeight, measuredItems, defaultHeight, overscan]);
    // 测量项目高度
    const measureItem = useCallback((id, height) => {
        setMeasuredItems(prev => {
            const newMap = new Map(prev);
            const existing = newMap.get(id);
            // 只有当高度变化时才更新
            if (!existing || existing.height !== height) {
                newMap.set(id, { id, height, offset: existing?.offset || 0 });
                // 更新后续项目的偏移量
                let offset = 0;
                for (let i = 0; i < items.length; i++) {
                    const itemId = `${items[i].role}-${i}`;
                    const item = newMap.get(itemId);
                    if (item) {
                        newMap.set(itemId, { ...item, offset });
                        offset += item.height;
                    }
                    else {
                        offset += defaultHeight;
                    }
                }
            }
            return newMap;
        });
    }, [items, defaultHeight]);
    // 处理滚动事件
    const handleScroll = useCallback(() => {
        if (containerRef.current) {
            setScrollTop(containerRef.current.scrollTop);
            setIsScrolling(true);
            // 清除之前的超时
            if (scrollTimeoutRef.current) {
                clearTimeout(scrollTimeoutRef.current);
            }
            // 设置新的超时
            scrollTimeoutRef.current = setTimeout(() => {
                setIsScrolling(false);
            }, 150);
        }
    }, [containerRef]);
    // 更新容器高度
    useEffect(() => {
        const updateContainerHeight = () => {
            if (containerRef.current) {
                setContainerHeight(containerRef.current.clientHeight);
            }
        };
        updateContainerHeight();
        window.addEventListener('resize', updateContainerHeight);
        return () => {
            window.removeEventListener('resize', updateContainerHeight);
            if (scrollTimeoutRef.current) {
                clearTimeout(scrollTimeoutRef.current);
            }
        };
    }, [containerRef]);
    // 添加滚动事件监听器
    useEffect(() => {
        const container = containerRef.current;
        if (container) {
            container.addEventListener('scroll', handleScroll, { passive: true });
            return () => {
                container.removeEventListener('scroll', handleScroll);
            };
        }
    }, [containerRef, handleScroll]);
    // 计算可见项目
    const visibleItems = useMemo(() => {
        const result = [];
        let offset = 0;
        for (let i = 0; i < items.length; i++) {
            const item = items[i];
            const measured = measuredItems.get(`${item.role}-${i}`);
            const itemHeight = measured?.height || defaultHeight;
            if (i >= visibleRange.startIndex && i < visibleRange.endIndex) {
                result.push({
                    item,
                    index: i,
                    offset,
                    height: itemHeight,
                });
            }
            offset += itemHeight;
        }
        return result;
    }, [items, measuredItems, visibleRange, defaultHeight]);
    // 滚动到指定项目
    const scrollToIndex = useCallback((index) => {
        if (containerRef.current) {
            let offset = 0;
            for (let i = 0; i < index && i < items.length; i++) {
                const measured = measuredItems.get(`${items[i].role}-${i}`);
                offset += measured?.height || defaultHeight;
            }
            containerRef.current.scrollTo({
                top: offset,
                behavior: 'smooth',
            });
        }
    }, [containerRef, items, measuredItems, defaultHeight]);
    // 滚动到底部
    const scrollToBottom = useCallback(() => {
        if (containerRef.current) {
            containerRef.current.scrollTo({
                top: totalHeight,
                behavior: 'smooth',
            });
        }
    }, [containerRef, totalHeight]);
    return {
        visibleItems,
        totalHeight,
        isScrolling,
        measureItem,
        scrollToIndex,
        scrollToBottom,
        scrollTop,
    };
};
