import { formatAstrolabeForAI } from "../../utils/astrolabeFormatter";
/**
 * 格式化星盘数据为AI可理解的格式（合盘模式专用）
 */
export const formatAstrolabeForSynastry = (astrolabe, currentTime = new Date()) => {
    if (!astrolabe) {
        return '暂无星盘数据';
    }
    const formattedTime = currentTime.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        hour12: false
    });
    // 精简版只包含最关键的信息
    let result = `
=== 紫微斗数星盘数据（精简版）===
当前时间：${formattedTime}

基本信息：
性别：${astrolabe.gender === 'male' ? '男' : '女'}
阳历日期：${astrolabe.solarDate}
干支纪年：${astrolabe.chineseDate}
出生时辰：${astrolabe.time}
命宫地支：${astrolabe.earthlyBranchOfSoulPalace}
身宫地支：${astrolabe.earthlyBranchOfBodyPalace}
命主：${astrolabe.soul}
身主：${astrolabe.body}
五行局：${astrolabe.fiveElementsClass}

关键宫位信息：
`;
    // 只添加关键宫位信息（命宫、身宫、夫妻宫、事业宫、财帛宫）
    const keyPalaces = ['命宫', '身宫', '夫妻宫', '事业宫', '财帛宫'];
    if (astrolabe.palaces && astrolabe.palaces.length > 0) {
        astrolabe.palaces.forEach((palace) => {
            if (keyPalaces.includes(palace.name)) {
                const majorStars = palace.majorStars?.map(star => star.name) || [];
                const minorStars = palace.minorStars?.map(star => star.name) || [];
                result += `
${palace.name}：
天干地支：${palace.heavenlyStem}${palace.earthlyBranch}
主星：${majorStars.join('、') || '无'}
辅星：${minorStars.join('、') || '无'}
`;
            }
        });
    }
    // 添加简化的大限和流年信息
    if (astrolabe.horoscope) {
        const horoscope = astrolabe.horoscope();
        if (horoscope) {
            result += `
当前运限：
大限：${horoscope.decadal.heavenlyStem}${horoscope.decadal.earthlyBranch}
流年：${horoscope.yearly.heavenlyStem}${horoscope.yearly.earthlyBranch}
`;
        }
    }
    return result.trim();
};
/**
 * 构建AI系统提示
 */
export const buildSystemPrompt = (formData, astrolabe, synastryAstrolabe, isSynastryMode, personNames) => {
    const currentTime = new Date();
    const basePrompt = `你是一位精通古法传承与现代应用的紫微斗数宗师。你的论断不仅基于星盘数据，更融入了深刻的人生智慧。请牢记以下心法：
1. **天人合一**：星盘是人生命运的蓝图，但人的意志和后天努力同样重要。你的解读应充满智慧和慈悲，旨在帮助问询者“知命而用命”，而非宿命论。
2. **言必有据**：所有的论断都必须严格依据所提供的星盘数据，结合宫、星、四化进行严谨的逻辑推演。
3. **结构清晰**：回答应有章法，先有“总论”概括全局，再“分项”详述，最后必须落脚于“建议”，为问询者提供具体、可行的趋吉避凶之道。
4. **口吻沉稳**：以一位学识渊博、洞察世事的长者口吻进行叙述，语言简练、沉稳、富有哲理。

`;
    if (isSynastryMode && synastryAstrolabe) {
        // 合盘模式下的系统提示
        const synastryData = formatAstrolabeForSynastry(synastryAstrolabe, currentTime);
        const person1Data = formatAstrolabeForSynastry(astrolabe, currentTime);
        return basePrompt + `现在，请根据以下两个人的星盘数据，为他们进行专业的“合盘”论断。

第一个人（${personNames.person1}）的星盘数据：
${person1Data}

第二个人（${personNames.person2}）的星盘数据：
${synastryData}

请运用你的全部学识，特别是“宫位互叠”与“飞星四化”等高级技法，深度剖析二人的缘分本质、互动模式与潜在的共业。你的分析需直指核心，点明关系中的关键，并提供维系与发展的智慧建议。`;
    }
    else {
        // 单人模式下的系统提示
        const gender = formData?.gender === 'male' ? '男' : '女';
        const astrolabeData = astrolabe ? formatAstrolabeForAI(astrolabe, currentTime, gender) : '暂无星盘数据';
        return basePrompt + `现在，请根据以下用户的星盘数据和当前时间，结合他/她的问题，进行专业的“命盘”解读。

${astrolabeData}

请运用你的全部学识，洞察星盘背后的天机，为问询者指点迷津。`;
    }
};
/**
 * 构建发送给AI的消息数组
 */
export const buildAIMessages = (systemPrompt, existingMessages, userMessage) => {
    return [
        { role: "system", content: systemPrompt },
        ...existingMessages,
        { role: userMessage.role, content: userMessage.content }
    ];
};
/**
 * 处理流式响应数据
 */
export const processStreamChunk = (chunk, buffer, onMessageUpdate) => {
    let assistantMessage = "";
    let newBuffer = buffer + chunk;
    // 按行分割，但保留可能的截断行
    const lines = newBuffer.split("\n");
    newBuffer = lines.pop() || ""; // 保留最后一个可能不完整的行
    for (const line of lines) {
        if (line.startsWith("data: ")) {
            const data = line.slice(6).trim();
            if (data === "[DONE]") {
                continue;
            }
            // 跳过空数据
            if (!data) {
                continue;
            }
            try {
                // 检查 JSON 数据是否完整
                if (data.startsWith('{') && data.endsWith('}')) {
                    const parsed = JSON.parse(data);
                    if (parsed.choices && parsed.choices[0].delta && parsed.choices[0].delta.content) {
                        const newContent = parsed.choices[0].delta.content;
                        assistantMessage += newContent;
                        // 立即通知更新新增的内容
                        onMessageUpdate(newContent);
                    }
                }
            }
            catch (e) {
                console.error("解析 SSE 数据时出错:", e);
                console.error("问题数据:", data);
            }
        }
    }
    return { newBuffer, assistantMessage };
};
/**
 * 处理缓冲区中剩余的数据
 */
export const processRemainingBuffer = (buffer, onMessageUpdate) => {
    let assistantMessage = "";
    if (buffer.trim()) {
        const lines = buffer.split("\n");
        let newBuffer = "";
        for (const line of lines) {
            if (line.startsWith("data: ")) {
                const data = line.slice(6).trim();
                if (data && data !== "[DONE]") {
                    try {
                        if (data.startsWith('{') && data.endsWith('}')) {
                            const parsed = JSON.parse(data);
                            if (parsed.choices && parsed.choices[0].delta && parsed.choices[0].delta.content) {
                                const newContent = parsed.choices[0].delta.content;
                                assistantMessage += newContent;
                                // 立即通知更新新增的内容
                                onMessageUpdate(newContent);
                            }
                        }
                    }
                    catch (e) {
                        console.error("解析缓冲区 SSE 数据时出错:", e);
                        console.error("问题数据:", data);
                    }
                }
            }
        }
        return newBuffer;
    }
    return buffer;
};
/**
 * 创建错误消息
 */
export const createErrorMessage = (error) => {
    console.error("AI 服务错误:", error);
    return {
        role: "assistant",
        content: "抱歉，我在处理您的消息时遇到了问题。请稍后再试。",
    };
};
/**
 * 验证消息内容
 */
export const isValidMessage = (message) => {
    return message &&
        typeof message.role === 'string' &&
        ['user', 'assistant'].includes(message.role) &&
        typeof message.content === 'string' &&
        message.content.trim().length > 0;
};
/**
 * 过滤无效消息
 */
export const filterValidMessages = (messages) => {
    return messages.filter(isValidMessage);
};
/**
 * 获取最后一条用户消息
 */
export const getLastUserMessage = (messages) => {
    const validMessages = filterValidMessages(messages);
    for (let i = validMessages.length - 1; i >= 0; i--) {
        if (validMessages[i].role === 'user') {
            return validMessages[i];
        }
    }
    return null;
};
