import { useState, useCallback, useRef } from 'react';
/**
 * 网络请求Hook
 * 提供缓存、重试和错误处理功能
 */
export const useNetworkRequest = (options = {}) => {
    const { cacheKey, cacheTTL = 5 * 60 * 1000, // 默认缓存5分钟
    maxRetries = 2, retryDelay = 1000, // 默认重试延迟1秒
    retryBackoff = 2, // 默认重试延迟倍数
     } = options;
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(null);
    const abortControllerRef = useRef(null);
    // 内存缓存
    const cacheRef = useRef(new Map());
    // 检查缓存是否有效
    const isCacheValid = useCallback((entry) => {
        return Date.now() - entry.timestamp < cacheTTL;
    }, [cacheTTL]);
    // 从缓存获取数据
    const getFromCache = useCallback((key) => {
        if (!cacheKey)
            return null;
        const fullKey = `${cacheKey}:${key}`;
        const entry = cacheRef.current.get(fullKey);
        if (entry && isCacheValid(entry)) {
            return entry.data;
        }
        return null;
    }, [cacheKey, isCacheValid]);
    // 设置缓存
    const setCache = useCallback((key, data) => {
        if (!cacheKey)
            return;
        const fullKey = `${cacheKey}:${key}`;
        cacheRef.current.set(fullKey, {
            data,
            timestamp: Date.now(),
        });
    }, [cacheKey]);
    // 清除缓存
    const clearCache = useCallback(() => {
        if (!cacheKey)
            return;
        // 清除特定前缀的缓存
        const keys = Array.from(cacheRef.current.keys());
        for (const key of keys) {
            if (key.startsWith(`${cacheKey}:`)) {
                cacheRef.current.delete(key);
            }
        }
    }, [cacheKey]);
    // 延迟函数
    const delay = useCallback((ms) => {
        return new Promise(resolve => setTimeout(resolve, ms));
    }, []);
    // 发送请求
    const sendRequest = useCallback(async (requestConfig, customCacheKey) => {
        const { url, options = {}, body } = requestConfig;
        // 如果有缓存，直接返回缓存数据
        const cacheData = getFromCache(customCacheKey || url);
        if (cacheData) {
            return cacheData;
        }
        // 取消之前的请求
        if (abortControllerRef.current) {
            abortControllerRef.current.abort();
        }
        // 创建新的AbortController
        abortControllerRef.current = new AbortController();
        setIsLoading(true);
        setError(null);
        let retryCount = 0;
        let currentRetryDelay = retryDelay;
        const attemptRequest = async () => {
            try {
                const fetchOptions = {
                    ...options,
                    signal: abortControllerRef.current?.signal,
                };
                if (body) {
                    fetchOptions.body = JSON.stringify(body);
                    if (!fetchOptions.headers) {
                        fetchOptions.headers = {};
                    }
                    fetchOptions.headers['Content-Type'] = 'application/json';
                }
                const response = await fetch(url, fetchOptions);
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                const data = await response.json();
                // 缓存响应数据
                setCache(customCacheKey || url, data);
                return data;
            }
            catch (err) {
                // 如果是手动中止的错误，不重试
                if (err instanceof Error && err.name === 'AbortError') {
                    throw err;
                }
                retryCount++;
                // 如果达到最大重试次数，抛出错误
                if (retryCount > maxRetries) {
                    setError(err);
                    throw err;
                }
                // 指数退避重试
                await delay(currentRetryDelay);
                currentRetryDelay *= retryBackoff;
                // 递归重试
                return attemptRequest();
            }
        };
        try {
            const result = await attemptRequest();
            return result;
        }
        finally {
            setIsLoading(false);
        }
    }, [
        getFromCache,
        setCache,
        retryDelay,
        retryBackoff,
        maxRetries,
        delay,
    ]);
    // 取消请求
    const cancelRequest = useCallback(() => {
        if (abortControllerRef.current) {
            abortControllerRef.current.abort();
            abortControllerRef.current = null;
        }
        setIsLoading(false);
    }, []);
    return {
        isLoading,
        error,
        sendRequest,
        cancelRequest,
        clearCache,
    };
};
/**
 * AI聊天专用的网络请求Hook
 * 针对AI聊天接口进行优化
 */
export const useAIChatRequest = () => {
    const { sendRequest, isLoading, error, clearCache } = useNetworkRequest({
        cacheKey: 'ai-chat',
        cacheTTL: 10 * 60 * 1000, // AI聊天缓存10分钟
        maxRetries: 3,
        retryDelay: 1000,
        retryBackoff: 1.5,
    });
    // 发送AI聊天请求
    const sendChatRequest = useCallback(async (messages, options = {}) => {
        const apiEndpoint = process.env.VITE_API_ENDPOINT;
        const apiKey = process.env.VITE_API_KEY;
        const defaultModel = process.env.VITE_MODEL || "gpt-3.5-turbo";
        if (!apiEndpoint) {
            throw new Error('VITE_API_ENDPOINT 环境变量未设置');
        }
        const { model = defaultModel, temperature = 0.7, stream = true } = options;
        // 构建完整的 API URL，支持基础 URL 自动添加 /chat/completions 路径
        const fullApiUrl = apiEndpoint.endsWith('/chat/completions')
            ? apiEndpoint
            : `${apiEndpoint}${apiEndpoint.endsWith('/') ? '' : '/'}chat/completions`;
        return sendRequest({
            url: fullApiUrl,
            options: {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    ...(apiKey && { "Authorization": `Bearer ${apiKey}` }),
                },
            },
            body: {
                model,
                messages,
                temperature,
                stream,
            },
        }, `chat:${JSON.stringify(messages.slice(-5))}`); // 使用最后5条消息作为缓存键
    }, [sendRequest]);
    return {
        isLoading,
        error,
        sendChatRequest,
        clearCache,
    };
};
