import { useState, useCallback, useRef } from 'react';
import { buildSystemPrompt, buildAIMessages, processStreamChunk, processRemainingBuffer, createErrorMessage } from '../utils/messageUtils';
import { useAIChatRequest } from './useNetworkRequest';
import { useAIChat } from '../AIChatContext';
/**
 * AI 聊天服务 Hook
 * 处理与 AI 服务的交互，包括发送消息、处理响应等
 */
export const useAIChatService = ({ formData, astrolabe, synastryAstrolabe, isSynastryMode = false, personNames = { person1: "第一个人", person2: "第二个人" }, onMessageUpdate, onMessagesChange, onLoadingChange, onGeneratingChange, onStreamingChange, onError, }) => {
    const [isGenerating, setIsGenerating] = useState(false);
    const abortControllerRef = useRef(null);
    const { model } = useAIChat();
    // 使用AI聊天请求Hook
    const { sendChatRequest, isLoading: isNetworkLoading, error: networkError } = useAIChatRequest();
    // 提取公共的流式处理逻辑
    const processStreamResponse = useCallback(async (response, existingMessages) => {
        // 处理流式响应
        const reader = response.body?.getReader();
        const decoder = new TextDecoder();
        let assistantMessageContent = "";
        let buffer = "";
        if (reader) {
            // 通知开始流式传输
            if (onStreamingChange) {
                onStreamingChange(true);
            }
            try {
                while (true) {
                    const { done, value } = await reader.read();
                    if (done)
                        break;
                    const chunk = decoder.decode(value, { stream: true });
                    const result = processStreamChunk(chunk, buffer, (newContent) => {
                        assistantMessageContent += newContent;
                        onMessageUpdate(assistantMessageContent);
                    });
                    buffer = result.newBuffer;
                }
            }
            catch (error) {
                // 如果是手动中止的错误，不显示错误消息
                if (error instanceof Error && error.name === 'AbortError') {
                    console.log('用户手动停止了生成');
                    return { content: "", shouldUpdate: false };
                }
                throw error;
            }
            // 处理缓冲区中剩余的数据
            buffer = processRemainingBuffer(buffer, (newContent) => {
                assistantMessageContent += newContent;
                onMessageUpdate(assistantMessageContent);
            });
            // 通知流式传输结束
            if (onStreamingChange) {
                onStreamingChange(false);
            }
        }
        return { content: assistantMessageContent, shouldUpdate: true };
    }, [onMessageUpdate, onStreamingChange]);
    // 提取公共的AI服务调用逻辑
    const callAIService = useCallback(async (messages, model) => {
        // 从环境变量获取配置
        const apiEndpoint = process.env.VITE_API_ENDPOINT;
        const apiKey = process.env.VITE_API_KEY;
        if (!apiEndpoint) {
            throw new Error('VITE_API_ENDPOINT 环境变量未设置');
        }
        // 构建完整的 API URL，支持基础 URL 自动添加 /chat/completions 路径
        const fullApiUrl = apiEndpoint.endsWith('/chat/completions')
            ? apiEndpoint
            : `${apiEndpoint}${apiEndpoint.endsWith('/') ? '' : '/'}chat/completions`;
        // 直接使用原始fetch方法，保持流式处理功能
        const response = await fetch(fullApiUrl, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                ...(apiKey && { "Authorization": `Bearer ${apiKey}` }),
            },
            body: JSON.stringify({
                model,
                messages,
                temperature: 0.7,
                stream: true,
            }),
            signal: abortControllerRef.current?.signal,
        });
        if (!response.ok) {
            throw new Error(`AI 服务错误: ${response.status} ${response.statusText}`);
        }
        return response;
    }, []);
    // 提取公共的错误处理逻辑
    const handleError = useCallback((error, userMessage, existingMessages) => {
        console.error("发送消息时出错:", error);
        // 移除最后一条空消息并添加错误消息
        const currentMessages = [...existingMessages];
        if (currentMessages[currentMessages.length - 1]?.role === "assistant" &&
            !currentMessages[currentMessages.length - 1]?.content.trim()) {
            currentMessages.pop();
        }
        const errorMessage = createErrorMessage(error);
        currentMessages.push(errorMessage);
        onMessagesChange(currentMessages);
        onError(true, userMessage.content);
    }, [onMessagesChange, onError]);
    // 发送消息到 AI 服务
    const sendMessage = useCallback(async (userMessage, existingMessages) => {
        if (!userMessage.content.trim())
            return;
        onLoadingChange(true);
        setIsGenerating(true);
        onError(false);
        try {
            // 创建新的 AbortController
            abortControllerRef.current = new AbortController();
            // 添加一个空的助手消息用于流式更新（用户消息已经在 existingMessages 中）
            const assistantMessage = { role: "assistant", content: "" };
            const updatedMessages = [...existingMessages, assistantMessage];
            onMessagesChange(updatedMessages);
            // 构建系统提示
            const systemPrompt = buildSystemPrompt(formData, astrolabe ?? null, synastryAstrolabe ?? null, isSynastryMode, personNames);
            // 构建发送给AI的消息数组
            const aiMessages = buildAIMessages(systemPrompt, existingMessages, userMessage);
            // 调用 AI 服务
            const response = await callAIService(aiMessages, model);
            // 处理流式响应
            const { content: assistantMessageContent, shouldUpdate } = await processStreamResponse(response, existingMessages);
            // 更新最终消息（用户消息已经在 existingMessages 中）
            if (shouldUpdate && assistantMessageContent.trim()) {
                const finalMessages = [...existingMessages, {
                        role: "assistant",
                        content: assistantMessageContent
                    }];
                onMessagesChange(finalMessages);
            }
            else {
                // 如果没有内容，移除空消息
                onMessagesChange(existingMessages);
            }
        }
        catch (error) {
            handleError(error, userMessage, existingMessages);
        }
        finally {
            onLoadingChange(false);
            setIsGenerating(false);
        }
    }, [
        astrolabe,
        synastryAstrolabe,
        isSynastryMode,
        personNames,
        onLoadingChange,
        onError,
        callAIService,
        processStreamResponse,
        handleError,
        onMessagesChange,
    ]);
    // 重新生成失败的回复
    const regenerateResponse = useCallback(async (failedMessage, existingMessages) => {
        if (!failedMessage.trim())
            return;
        onError(false);
        onLoadingChange(true);
        setIsGenerating(true);
        try {
            // 创建新的 AbortController
            abortControllerRef.current = new AbortController();
            // 添加一个空的助手消息用于流式更新（用户消息已经在 existingMessages 中）
            const assistantMessage = { role: "assistant", content: "" };
            const updatedMessages = [...existingMessages, assistantMessage];
            onMessagesChange(updatedMessages);
            // 构建系统提示
            const systemPrompt = buildSystemPrompt(formData, astrolabe ?? null, synastryAstrolabe ?? null, isSynastryMode, personNames);
            // 构建发送给AI的消息数组（排除最后一条失败的助手消息）
            const messagesWithoutLastAssistant = existingMessages.filter(msg => !(msg.role === "assistant" && msg === existingMessages[existingMessages.length - 1]));
            const aiMessages = [
                { role: "system", content: systemPrompt },
                ...messagesWithoutLastAssistant,
                { role: "user", content: failedMessage }
            ];
            // 调用 AI 服务
            const response = await callAIService(aiMessages, model);
            // 处理流式响应
            const { content: assistantMessageContent, shouldUpdate } = await processStreamResponse(response, existingMessages);
            // 更新最终消息（用户消息已经在 messagesWithoutLastAssistant 中）
            if (shouldUpdate && assistantMessageContent.trim()) {
                const finalMessages = [...messagesWithoutLastAssistant, {
                        role: "assistant",
                        content: assistantMessageContent
                    }];
                onMessagesChange(finalMessages);
            }
            else {
                // 如果没有内容，恢复原始消息
                onMessagesChange(existingMessages);
            }
        }
        catch (error) {
            console.error("重新生成消息时出错:", error);
            // 恢复原始消息并添加错误消息
            const errorMessage = createErrorMessage(error);
            const finalMessages = [...existingMessages, errorMessage];
            onMessagesChange(finalMessages);
            onError(true, failedMessage);
        }
        finally {
            onLoadingChange(false);
            setIsGenerating(false);
        }
    }, [
        astrolabe,
        synastryAstrolabe,
        isSynastryMode,
        personNames,
        onError,
        onLoadingChange,
        callAIService,
        processStreamResponse,
        onMessagesChange,
        model,
    ]);
    // 停止生成
    const stopGeneration = useCallback(() => {
        if (abortControllerRef.current) {
            abortControllerRef.current.abort();
            abortControllerRef.current = null;
        }
        setIsGenerating(false);
        onGeneratingChange(false);
        onLoadingChange(false);
    }, [onGeneratingChange, onLoadingChange]);
    return {
        isGenerating,
        sendMessage,
        regenerateResponse,
        stopGeneration,
    };
};
