import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { useAIChat } from '../AIChatContext';
import { useMessageScroll } from '../hooks/useMessageScroll';
import { useVirtualKeyboard } from '../hooks/useVirtualKeyboard';
import { useAIChatService } from '../hooks/useAIChatService';
import { useChatLayout } from '../hooks/useChatLayout';
import MessageList from './MessageList';
import ChatInput from './ChatInput';
import QuickActions from './QuickActions';
import QuestionPanel from './QuestionPanel';
import SkeletonLoader from './SkeletonLoader';
import ScrollToBottomButton from './ScrollToBottomButton';
import '../../styles/chat/model-selector.css';
// 使用React.memo优化组件重渲染
const ChatContainerComponent = ({ formData, astrolabe, synastryAstrolabe, isSynastryMode = false, personNames = { person1: "第一个人", person2: "第二个人" }, isVisible = true, }) => {
    const { activeSession, setMessages, isLoading, setIsLoading, updateLastMessage, updateSessionTitle, model, setModel, } = useAIChat();
    const messages = useMemo(() => activeSession?.messages ?? [], [activeSession]);
    // 本地状态管理
    const [inputValue, setInputValue] = useState("");
    const [showQuestionPanel, setShowQuestionPanel] = useState(false);
    const [isGenerating, setIsGenerating] = useState(false);
    const [isStreaming, setIsStreaming] = useState(false);
    const [generationFailed, setGenerationFailed] = useState(false);
    const [lastFailedMessage, setLastFailedMessage] = useState("");
    const [activeQuestionTab, setActiveQuestionTab] = useState("感情");
    // 输入框引用
    const inputRef = React.useRef(null);
    // 懒加载数据
    const [quickActionsData, setQuickActionsData] = useState([]);
    const [synastryQuickActionsData, setSynastryQuickActionsData] = useState([]);
    const [questionCategoriesData, setQuestionCategoriesData] = useState([]);
    const [dataLoaded, setDataLoaded] = useState(false);
    // 使用自定义 Hooks
    const { messagesContainerRef, lastMessageRef, userScrolledUp, setUserScrolledUp, scrollToBottom, } = useMessageScroll(messages, isVisible, isStreaming);
    const { keyboardState, setupInputListeners } = useVirtualKeyboard();
    const { getContainerStyle, getMessagesContainerStyle, getInputContainerStyle } = useChatLayout();
    // AI 聊天服务
    const { sendMessage, regenerateResponse, stopGeneration } = useAIChatService({
        formData,
        astrolabe,
        synastryAstrolabe,
        isSynastryMode,
        personNames,
        onMessageUpdate: updateLastMessage,
        onMessagesChange: setMessages,
        onLoadingChange: setIsLoading,
        onGeneratingChange: setIsGenerating,
        onStreamingChange: setIsStreaming,
        onError: (error, message) => {
            setGenerationFailed(error);
            if (message)
                setLastFailedMessage(message);
        },
    });
    // 自动为新会话生成标题
    useEffect(() => {
        if (activeSession &&
            activeSession.title === '新的对话' &&
            activeSession.messages.length >= 2 &&
            activeSession.messages[0].role === 'user') {
            // 使用第一个用户问题作为标题，并截断
            const newTitle = activeSession.messages[0].content.substring(0, 20);
            updateSessionTitle(activeSession.id, newTitle);
        }
    }, [activeSession, updateSessionTitle]);
    // 懒加载数据
    useEffect(() => {
        const loadData = async () => {
            try {
                // 动态导入预设数据
                const { quickActions, synastryQuickActions, questionCategories } = await import('../presets');
                setQuickActionsData(quickActions);
                setSynastryQuickActionsData(synastryQuickActions);
                setQuestionCategoriesData(questionCategories);
                setDataLoaded(true);
            }
            catch (error) {
                console.error('加载数据失败:', error);
                setDataLoaded(true); // 即使失败也设置为已加载，避免无限加载状态
            }
        };
        loadData();
    }, []);
    // 处理虚拟键盘弹出时隐藏问题面板
    useEffect(() => {
        if (keyboardState.isVisible && showQuestionPanel) {
            setShowQuestionPanel(false);
        }
    }, [keyboardState.isVisible, showQuestionPanel]);
    // 使用useCallback优化事件处理函数
    const handleSendMessage = useCallback(async (content) => {
        if (!content.trim() || isLoading)
            return;
        const userMessage = { role: "user", content };
        const updatedMessages = [...messages, userMessage];
        setMessages(updatedMessages); // This now updates the active session
        setInputValue("");
        setGenerationFailed(false);
        setLastFailedMessage(content);
        setShowQuestionPanel(false);
        await sendMessage(userMessage, updatedMessages);
    }, [isLoading, messages, sendMessage, setMessages]);
    const handleQuickActionClick = useCallback(async (name, prompt) => {
        if (!name.trim() || isLoading)
            return;
        const userMessage = { role: "user", content: name };
        const updatedMessages = [...messages, userMessage];
        setMessages(updatedMessages); // This now updates the active session
        setInputValue("");
        setGenerationFailed(false);
        setLastFailedMessage(name);
        setShowQuestionPanel(false);
        await sendMessage({ ...userMessage, content: prompt }, updatedMessages);
    }, [isLoading, messages, sendMessage, setMessages]);
    const handlePresetQuestionClick = useCallback((question) => {
        handleSendMessage(question);
    }, [handleSendMessage]);
    const handleSubmit = useCallback((e) => {
        e.preventDefault();
        handleSendMessage(inputValue);
    }, [handleSendMessage, inputValue]);
    const toggleQuestionPanel = useCallback(() => {
        setShowQuestionPanel(!showQuestionPanel);
    }, [showQuestionPanel]);
    const handleRegenerate = useCallback(async () => {
        if (!lastFailedMessage.trim())
            return;
        setGenerationFailed(false);
        await regenerateResponse(lastFailedMessage, messages);
    }, [lastFailedMessage, messages, regenerateResponse]);
    const handleMessagesClick = useCallback(() => {
        if (showQuestionPanel) {
            setShowQuestionPanel(false);
        }
    }, [showQuestionPanel]);
    if (!isVisible) {
        return null;
    }
    return (React.createElement("div", { className: `ai-chat ${isVisible ? "visible" : "hidden"}` },
        React.createElement(MessageList, { messages: messages, isLoading: isLoading, generationFailed: generationFailed, lastFailedMessage: lastFailedMessage, onRegenerate: handleRegenerate, messagesContainerRef: messagesContainerRef, lastMessageRef: lastMessageRef, isStreaming: isStreaming }),
        React.createElement("div", { className: "chat-input-container", onClick: (e) => e.stopPropagation() }, !dataLoaded ? (
        // 数据加载时显示骨架屏
        React.createElement("div", { className: "chat-skeleton-fallback" },
            React.createElement(SkeletonLoader, { type: "input", animate: true }))) : (React.createElement(React.Fragment, null,
            React.createElement(QuickActions, { quickActions: isSynastryMode ? synastryQuickActionsData : quickActionsData, onQuickActionClick: handleQuickActionClick, isLoading: isLoading, model: model, setModel: setModel, models: ["sydf/v1-250520", "qwen/qwen3-max", "ovo/deepseek-v3.1", "ovo/glm-4.5", "ovo/kimi-k2-0905", "ovo/gemini-2.5-pro", "ovo/gpt-oss-120", "ovo/llama-v4-maverick"] }),
            React.createElement(ChatInput, { inputValue: inputValue, onInputChange: setInputValue, onSubmit: handleSubmit, onToggleQuestionPanel: toggleQuestionPanel, onStopGeneration: stopGeneration, isGenerating: isGenerating, isLoading: isLoading, showQuestionPanel: showQuestionPanel, inputRef: inputRef, setupInputListeners: setupInputListeners }),
            React.createElement(QuestionPanel, { questionCategories: questionCategoriesData, activeQuestionTab: activeQuestionTab, onTabChange: setActiveQuestionTab, onQuestionClick: handlePresetQuestionClick, showQuestionPanel: showQuestionPanel })))),
        React.createElement(ScrollToBottomButton, { isVisible: userScrolledUp && (isStreaming || isLoading), onClick: () => {
                scrollToBottom(true);
                setUserScrolledUp(false);
            } })));
};
// 使用React.memo优化组件重渲染
const ChatContainer = React.memo(ChatContainerComponent);
export default ChatContainer;
