import React, { useEffect, useState, useMemo, useCallback } from 'react';
// 使用React.memo优化组件重渲染
const ChatInputComponent = ({ inputValue, onInputChange, onSubmit, onToggleQuestionPanel, onStopGeneration, isGenerating, isLoading, showQuestionPanel, inputRef, setupInputListeners, }) => {
    // 检测是否为 PWA 模式
    const [isPWA, setIsPWA] = useState(false);
    // 检测是否为 iOS 设备
    const [isIOS, setIsIOS] = useState(false);
    // 检测是否为安卓设备
    const [isAndroid, setIsAndroid] = useState(false);
    // 安卓导航栏高度
    const [androidNavBarHeight, setAndroidNavBarHeight] = useState(0);
    // iOS 刘海区域高度
    const [iosNotchHeight, setIosNotchHeight] = useState(0);
    // 优化设备检测逻辑，使用useMemo缓存检测结果
    const deviceInfo = useMemo(() => {
        const isIOSDevice = /iPhone|iPad|iPod/i.test(navigator.userAgent);
        const isAndroidDevice = /Android/i.test(navigator.userAgent);
        const isStandalone = window.matchMedia('(display-mode: standalone)').matches ||
            window.navigator.standalone ||
            document.referrer.includes('android-app://');
        return {
            isIOSDevice,
            isAndroidDevice,
            isStandalone
        };
    }, []);
    // 优化高度计算逻辑，使用useMemo缓存计算结果
    const heights = useMemo(() => {
        const getIosNotchHeight = () => {
            if (!deviceInfo.isIOSDevice)
                return 0;
            const safeAreaTop = parseInt(getComputedStyle(document.documentElement)
                .getPropertyValue('--safe-area-inset-top')) || 0;
            // iOS 刘海区域通常在 20-44px 之间
            return safeAreaTop > 0 ? Math.max(safeAreaTop, 20) : 0;
        };
        const getAndroidNavBarHeight = () => {
            if (!deviceInfo.isAndroidDevice)
                return 0;
            const safeAreaBottom = parseInt(getComputedStyle(document.documentElement)
                .getPropertyValue('--safe-area-inset-bottom')) || 0;
            const androidNavBarHeight = parseInt(getComputedStyle(document.documentElement)
                .getPropertyValue('--android-navigation-bar-height')) || 0;
            return Math.max(safeAreaBottom, androidNavBarHeight);
        };
        return {
            iosNotchHeight: getIosNotchHeight(),
            androidNavBarHeight: getAndroidNavBarHeight()
        };
    }, [deviceInfo]);
    // 优化事件处理函数，使用useCallback
    const handleInputChange = useCallback((e) => {
        onInputChange(e.target.value);
    }, [onInputChange]);
    const handleButtonClick = useCallback(() => {
        if (isGenerating) {
            onStopGeneration();
        }
        else if (inputValue.trim()) {
            onSubmit(new Event('submit'));
        }
        else {
            onToggleQuestionPanel();
        }
    }, [isGenerating, inputValue, onStopGeneration, onSubmit, onToggleQuestionPanel]);
    // 优化设备检测和状态更新
    useEffect(() => {
        setIsPWA(deviceInfo.isStandalone);
        setIsIOS(deviceInfo.isIOSDevice);
        setIsAndroid(deviceInfo.isAndroidDevice);
        setIosNotchHeight(heights.iosNotchHeight);
        setAndroidNavBarHeight(heights.androidNavBarHeight);
    }, [deviceInfo, heights]);
    // 优化事件监听器设置
    useEffect(() => {
        // 监听显示模式变化
        const mediaQuery = window.matchMedia('(display-mode: standalone)');
        const handlePWAChange = () => setIsPWA(mediaQuery.matches);
        mediaQuery.addEventListener('change', handlePWAChange);
        // 监听CSS变量变化（导航栏高度可能会变化）
        const observer = new MutationObserver(() => {
            if (deviceInfo.isAndroidDevice) {
                const safeAreaBottom = parseInt(getComputedStyle(document.documentElement)
                    .getPropertyValue('--safe-area-inset-bottom')) || 0;
                const androidNavBarHeight = parseInt(getComputedStyle(document.documentElement)
                    .getPropertyValue('--android-navigation-bar-height')) || 0;
                setAndroidNavBarHeight(Math.max(safeAreaBottom, androidNavBarHeight));
            }
        });
        observer.observe(document.documentElement, {
            attributes: true,
            attributeFilter: ['style']
        });
        return () => {
            mediaQuery.removeEventListener('change', handlePWAChange);
            observer.disconnect();
        };
    }, [deviceInfo]);
    // 设置输入框监听器
    useEffect(() => {
        if (inputRef.current) {
            setupInputListeners(inputRef.current);
        }
    }, [inputRef, setupInputListeners]);
    return (React.createElement("form", { className: `chat-input chat-input-form ${isPWA ? 'pwa-mode' : ''} ${isIOS ? 'ios-mode' : ''} ${isAndroid ? 'android-mode' : ''}`, onSubmit: onSubmit },
        React.createElement("input", { ref: inputRef, type: "text", value: inputValue, onChange: handleInputChange, placeholder: "\u8F93\u5165\u60A8\u7684\u95EE\u9898...", disabled: isLoading, className: "chat-input-field" }),
        React.createElement("button", { type: isGenerating ? "button" : (inputValue.trim() ? "submit" : "button"), onClick: handleButtonClick, disabled: isLoading && !isGenerating, className: "chat-action-button chat-send-button" }, isGenerating ? (React.createElement("span", { className: "chat-stop-icon" }, "\u25A0")) : inputValue.trim() ? (React.createElement("svg", { xmlns: "http://www.w3.org/2000/svg", width: "24", height: "24", viewBox: "0 0 24 24", fill: "#ffffff", stroke: "#ffffff", strokeWidth: "2", strokeLinecap: "round", strokeLinejoin: "round", className: "chat-send-icon" },
            React.createElement("line", { x1: "22", y1: "2", x2: "11", y2: "13" }),
            React.createElement("polygon", { points: "22 2 15 22 11 13 2 9 22 2" }))) : (React.createElement("span", { className: "chat-toggle-icon" }, showQuestionPanel ? "✕" : "+")))));
};
// 使用React.memo优化组件重渲染
const ChatInput = React.memo(ChatInputComponent);
export default ChatInput;
